@charset "UTF-8";
.blind {
  width: 1px;
  height: 1px;
  margin: 0 !important;
  padding: 0 !important;
  line-height: 1px;
  text-indent: -9999em;
  overflow: hidden;
  position: absolute;
  top: -9999em;
  left: -9999em;
}

#page .page-body .data-finder {
  margin: 20px 0;
}
#page .page-body .pagination {
  margin: 40px 0 0;
}

.page-board {
  padding: 0 32px 40px 32px;
  box-sizing: border-box;
  border-radius: 16px;
  overflow-y: auto;
}
@media (max-width: 1023px) {
  .page-board {
    overflow: auto;
  }
}
@media (min-width: 1024px) {
  .page-board {
    -webkit-overflow-scrolling: touch;
    overflow: overlay;
  }
  .page-board::-webkit-scrollbar-thumb {
    border-width: 4px;
    border-style: solid;
    border-color: transparent;
    background-clip: content-box;
    background-color: rgba(0, 0, 0, 0.2);
  }
  .page-board::-webkit-scrollbar-thumb:hover {
    background-color: rgba(0, 0, 0, 0.4);
  }
  .page-board::-webkit-scrollbar-thumb:active {
    background-color: rgba(0, 0, 0, 0.6);
  }
  .page-board::-webkit-scrollbar {
    width: 16px;
    height: 16px;
  }
  .page-board::-webkit-scrollbar-thumb {
    border-radius: 8px;
  }
}
