@charset "UTF-8";

@import 'partials/_mixin';
@import 'partials/_variables';


.blind {
	width: 1px;
	height: 1px;
	margin: 0 !important;
	padding: 0 !important;
	line-height: 1px;
	text-indent: -9999em;
	overflow: hidden;
	position: absolute;
	top: -9999em;
	left: -9999em;
}

// page
#page {
	// background-color: red;
	.page-body {
		.data-finder {
			margin: 20px 0;
		}

		.pagination {
			margin: 40px 0 0;
		}
	}
}


.page-board {
	// height: 100vh;
	padding: 0 32px 40px 32px;
	//margin: 28px;
	box-sizing: border-box;
	border-radius: 16px;
	overflow-y: auto;
	@include overflow-scroll(default, dark, large);
	// background-color: var(--color-base-100);

	.btn-a.medium.reset {
		width: 40px;
		padding: 0;

		&::before {
			content: '';
			display: block;
			width: 20px;
			height: 20px;
			mask-image: icon(reset);
			mask-repeat: no-repeat;
			background-color:var(--color-base-010);
			mask-position: center;
		}

		.btn-text {@include hidden;}
	}
}



/********************************************************************************************************************************

 * 개별 화면

********************************************************************************************************************************/

// 로그인, system에러페이지
.login-wrap,
.system-wrap {
	background-color: var(--color-base-100);
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	.login-header {
		position: fixed;
		top: 32px;
		left: 40px;
	
		.login-subject {
			.logo-name {
				&::before {
					content: '';
					display: block;
					width: 155px;
					height: 20px;
					mask-image: url(../images/ucomp-logo.svg);
					mask-repeat: no-repeat;
					mask-size: cover;
					mask-position: center;
					background-color: var(--color-primary-010);
				}
	
				.logo-text {
					@include hidden
				}
			}
		}
	}

	.login-container {
		.page-login {
			display: flex;
			flex-direction: column;
			align-items: center;

			.page-head {
				.title-m {
					line-height: 26px;
					font-weight: 700;
					margin-bottom: 32px;
				}
			}
		
			//로그인
			.login-form {
				display: flex;
				flex-direction: column;
				row-gap: 40px;
				width: 320px;
				margin: 0 auto;
		
				.login-form-head {
					text-align: center;
				}
		
				.login-form-body {
					.form-group {
						display: flex;
						flex-direction: column;
						text-align: left;
		
						&.option {
							margin: 20px 0;
						}
		
						+.form-group {
							margin-top: 16px;
						}

						.form.checkbox {
							--chk-size: 17px;

							.form-elem{
								width: 24px;
								height: 24px;
							}

							.form-text {
								line-height: 24px;
								padding-left: 30px;
							}
						}
					}
		
					.button-area {
						margin: 24px 0 0;
					}
		
					.find-account {
						display: flex;
						justify-content: center;
						align-items: center;
						margin-top: 32px;
		
						.navi-list {
							display: flex;
							gap: 12px;
		
							.navi-item {
								display: flex;
								align-items: center;
								gap: 12px;
		
								+.navi-item {
									&::before {
										content: "";
										display: block;
										width: 1px;
										height: 12px;
										background-color: var(--color-base-050);
									}
								}

								.navi-text {
									line-height: 20px;
									font-size: 14px;
									color: var(--color-base-030);

									&:focus {
										outline-offset: 1px;
										outline: 2px solid rgba(36, 107, 235, 0.8);
										border-radius: 4px;
									}
								}
							}
						}
					}


					&.invalid {
						.form-group {
							.form.input.style-b {
								.form-elem {
									border-bottom-color: #ff314e;
								}
							}

							&.noti {
								margin-top: 24px;

								.invalid-noti {
									line-height: 20px;
									font-size: 14px;
									color: var(--color-primary-010);
								}

							}
						}
					}
				}
			}
		}
	}

	.system-container {
		.inner-bx-ty01 {
			width: 100%;
			display: flex;
			flex-direction: column;
			align-items: center;
	
			.info-board {
				display: flex;
				flex-direction: column;
				align-items: center;
				row-gap: 48px;
	
				.board-wrap {
					display: flex;
					flex-direction: column;
					align-items: center;
					row-gap: 16px;
	
					.board-head {
						.board-sub {
							.board-name {
								line-height: 40px;
								font-size: 28px;
								color: var(--color-base-050);
								font-weight: 700;
							}
						}
					}
	
					&::after {
						content: '';
						display: block;
						width: 160px;
						height: 160px;
						background-repeat: no-repeat;
						background-size: cover;
						background-position: center;
					}
				}
				.board-side {
					text-align: center;
	
					.sub-tx {
						line-height: 30px;
						font-size: 20px;
						color: var(--color-base-010);
						font-weight: bold;
					}
					.sum-tx {
						line-height: 24px;
						font-size: 16px;
						margin-top: 8px;
						color: var(--color-base-020);
	
						.wbr {display: block;}
					}
				}
	
				&.attr-check {
					.board-wrap {
						&::after {
							background-image: url(./../../admin/images/system-check.svg);
						}
					}
				}
	
				&.attr-no-data {
					.board-wrap {
						&::after {
							background-image: url(./../../admin/images/no-data.svg);
						}
					}
				}
	
				&.attr-error {
					.board-wrap {
						&::after {
							background-image: url(./../../admin/images/error.svg);
						}
					}
				}
			}
	
			.data-list {
				margin-top: 48px;
				padding: 16px 83px;
				border-radius: 4px;
				background-color: var(--color-base-070);
				min-width: 400px;
				box-sizing: border-box;
	
				.data-item {
					display: flex;
					align-items: center;
					column-gap: 0.25em;
					line-height: 20px;
					font-size: 14px;
					font-weight: 500;
					color: var(--color-base-010);
					font-weight: 500;

					&::before {
						content: '';
						display: block;
						width: 3px;
						height: 3px;
						background-color: var(--color-base-010);
						border-radius: 50%;
					}
				}
			}
	
			.button-area {
				margin-top: 48px;
				column-gap: 8px;

				.btn-a {
					padding: 0;
					min-width: 196px;
				}
			}
		}
	}
}


// 회원 상세 pop
.pop-mem-wrap {
	.pop-container {
		padding: 24px 32px;
		background: var(--color-base-100);

		.data-display.member-info {
			padding-bottom: 24px;
			display: flex;
			align-items: flex-start;
			justify-content: space-between;
			border-bottom: 1px solid var(--color-base-060);

			.data-list {
				.data-item {
					.data-wrap {
						display: flex;
						align-items: center;
						column-gap: 12px;

						.data-head {
							display: flex;
							align-items: center;
							line-height: 40px;
							font-size: 26px;

							.data-subject {
								.data-name {
									color: var(--color-base-010);
									font-weight: 700;
								}
							}

							.data-summary {
								display: flex;
								align-items: center;
								color: var(--color-base-040);

								&::before {
									content: '/';
									display: block;
									margin: 0 0.25em;
								}
							}
						}

						.data-body {
							.mark-list {
								display: flex;
								column-gap: 6px;

								.mark-item {
									padding: 2px 8px;
									border-radius: 90px;
									box-sizing: border-box;
									line-height: 20px;
									font-size: 14px;
									font-weight: 600;
									color: var(--color-base-100);

									&.type-01 {
										background-color: #A8B6C5;
									}

									&.type-02 {
										background-color: #0E85FF;
									}

									&.type-03 {
										background-color: #00C48A;
									}
								}
							}
						}
					}
				}
			}

			.button-area {
				column-gap: 6px;
			}
		}

		.section.info-bx {
			margin-top: 24px;

			.data-display {
				.data-list {
					display: flex;
					column-gap: 24px;

					.data-item {
						.data-wrap {
							display: flex;
							flex-direction: column;
							row-gap: 12px;

							.data-head {
								display: flex;
								align-items: center;
								justify-content: space-between;

								.data-name {
									line-height: 30px;
									font-size: 20px;
									font-weight: 700;
								}

								.button-area {
									column-gap: 6px;
								}
							}
						}
					}
				}	
			}
		}
	}
}





